@charset "UTF-8";

/* reset */
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, textarea, p, blockquote, th, td, input, select, textarea, button {
  margin: 0;
  padding: 0
}

fieldset, img {
  border: 0 none
}

dl, ul, ol, menu, li {
  list-style: none
}

blockquote, q {
  quotes: none
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none
}

input, select, textarea, button {
  vertical-align: middle;
  outline: none
}

input::-ms-clear {
  display: none
}

button {
  border: 0 none;
  background-color: transparent;
  cursor: pointer
}

body {
  background: #fff;
  -webkit-text-size-adjust: none
}

body, th, td, input, select, textarea, button {
  font-size: 14px;
  line-height: 1.5;
  font-family: 'Spoqa Han Sans', sans-serif;
  color: #666
}

a {
  color: #333;
  text-decoration: none
}

a:active, a:hover {
  text-decoration: underline
}

a:active {
  background-color: transparent
}

z address, caption, cite, code, dfn, em, var {
  font-style: normal;
  font-weight: normal
}

input:checked[type='checkbox'] {
  background-color: #666;
  -webkit-appearance: checkbox
}

input[type='text'], input[type='password'], input[type='submit'], input[type='search'], input[type='tel'], input[type='email'], html input[type='button'], input[type='reset'] {
  -webkit-appearance: none;
  border-radius: 0
}

input[type='search']::-webkit-search-cancel-button, textarea {
  -webkit-appearance: none
}

/* global */
#dkIndex {
  overflow: hidden;
  position: absolute;
  left: -9999px;
  width: 0;
  height: 1px;
  margin: 0;
  padding: 0
}

/*  */
.ir_pm {
  display: block;
  overflow: hidden;
  font-size: 0px;
  line-height: 0;
  text-indent: -9999px
}

/*       */
.ir_wa {
  display: block;
  overflow: hidden;
  position: relative;
  z-index: -1;
  width: 100%;
  height: 100%
}

/*    off     */
.screen_out {
  overflow: hidden;
  position: absolute;
  width: 0;
  height: 0;
  line-height: 0;
  text-indent: -9999px
}

/*       */
.show {
  display: block
}

.hide {
  display: none
}

.ico_focused {
  outline: 1px dotted #000;
  outline: -webkit-focus-ring-color auto 5px
}


html, body {
  height: 100%;
  min-height: 100%
}

